<template>
  <el-dialog :append-to-body="true" title="客户选择" :visible.sync="customerShow" width="70%" :close-on-click-modal="false" :close-on-press-escape="false" :show-close="false">
    <!-- 筛选条件 -->
    <el-card class="filter-container" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right" @click="getData" type="primary" size="small">查询</el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small">重置</el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="客户身份">
            <el-select v-model="searchList.vipType" placeholder="请选择客户身份">
              <el-option label="会员用户" value="1"></el-option>
              <el-option label="普通用户" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="联系方式：">
            <el-input style="width: 203px" v-model="searchList.cellPhone" clearable placeholder="联系方式" @input="change($event)"></el-input>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <el-card class="filter-container" shadow="never" style="margin-top:10px;">
      <el-table
        ref="multipleTable"
        :data="list"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column label="用户昵称" prop="nikeName"></el-table-column>
        <el-table-column label="联系方式" width="240">
          <template slot-scope="scope">{{scope.row.cellPhone ? scope.row.cellPhone : '-'}}</template>
        </el-table-column>
        <el-table-column label="来源" prop="sourType"></el-table-column>
        <el-table-column label="客户身份" prop="vipTypeName"></el-table-column>
        <el-table-column label="注册时间" prop="createTime"></el-table-column>
      </el-table>
      <div style="padding:10px 0 20px 0;">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="searchList.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="listTotal">
        </el-pagination>
      </div>
    </el-card>
    <span slot="footer" class="dialog-footer">
      <el-button @click="close">取 消</el-button>
      <el-button type="primary" @click="sub">确 定</el-button>
    </span>
  </el-dialog>
</template>
<script>
import { mapState } from 'vuex'
import { getCouponUserPage } from '@/api/marketManage/coupon'

export default {
  props: ["userIds","customerShow"],
  watch:{
    customerShow(newvalue,oldValue){
      if(newvalue){
        this.getData();
      }
    },
  },
  data(){
    return{
      fromLoading:false,
      searchList:{
        shopCategoryIdArr:[],
        pageNum:1,
        pageSize:5
      },
      category:[],
      list:[],
      listTotal:0,
      multipleSelection:[],           // 勾选的数据
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
  },
  methods:{
    sub(){
      console.log('multipleSelection',this.multipleSelection)
      if(this.multipleSelection.length == 0){
        this.$message.error('请选择客户后再试');
        return;
      }
      this.$emit('customerFun',this.multipleSelection)
      this.$refs.multipleTable.clearSelection();
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.searchList.pageSize = val;
      this.getData()
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.searchList.pageNum = val;
      this.getData()
    },
    reset(){
      this.searchList.vipType = ''
      this.searchList.cellPhone = []
      this.getData();
    },
    // 列表 - 获取数据
    getData(){
      this.fromLoading = true
      this.searchList.userIds = this.userIds || ''
      console.log('请求的数据',this.searchList)
      getCouponUserPage(this.searchList).then(res=>{
        console.log('可添加优惠券的商品列表',res)
        if(res.code == 200){
          this.list = res.data.list
          this.listTotal = res.data.total
        }
        this.fromLoading = false
      })
    },
    // 获取全部分类
    getCategory(){
      getProductCategoryShopAllList(this.shop.shopId).then(res=>{
        console.log('获取店铺分类',res)
        this.category = res.data
      })
    },
    close(){
      this.$emit('customerClose',true)
    },
    change(){
      this.$forceUpdate()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>