<template>
  <div class="pageContent">
    <el-form ref="form" :model="form" label-width="160px">
      <el-card class="filter-container" shadow="never">
        <div class="pageHead">基本信息</div>
        <el-form-item label="优惠券名称：" required>
          <el-input 
            :disabled="form.couponStatus != 0 "
            v-model="form.couponName" 
            style="width:400px;"
            maxlength="10"
            show-word-limit
            placeholder="如：年货节优惠券，最多10个字"
          ></el-input>
        </el-form-item>
        <el-form-item class="inner" label="发放总量：" required>
          <el-input-number 
            class="tal"
            v-model="form.couponCount" 
            step-strictly
            :min="1"
            :max="100000000"
            style="width:400px;"
            :controls="false"
            placeholder="最多100000000张"
          ></el-input-number>
          <div class="infoFont">修改优惠券总量时只能增加不能减少，请谨慎设置</div>
        </el-form-item>
        <el-form-item label="适用商品：" required>
          <el-radio-group v-model="form.productType">
            <el-radio :disabled="form.couponStatus != 0 " :label="0">全部商品可用</el-radio>
            <el-radio :disabled="form.couponStatus != 0 " :label="1">指定商品可用</el-radio>
            <el-radio :disabled="form.couponStatus != 0 " :label="2">指定商品不可用</el-radio>
          </el-radio-group>
          <div class="infoFont">若活动商品中存在分销商品，优惠后可能造成利润受损</div>
        </el-form-item>
        <el-form-item v-if="form.productType == 1" label="选择商品：" required>
          <div><el-button :disabled="form.couponStatus != 0 " @click="addCom(1)">添加商品</el-button></div>
          <div v-if="useProList.length > 0" class="mt10">
            <el-table :data="useProList" style="width: 100%">
              <el-table-column label="商品图片" width="140">
                <template slot-scope="scope">
                  <el-popover placement="right" title trigger="hover">
                    <el-image
                      slot="reference"
                      :src="scope.row.imageUrl"
                      :alt="scope.row.imageUrl"
                      style="max-height: 40px;max-width: 40px"
                    ></el-image>
                    <el-image :src="scope.row.imageUrl" style="max-height: 350px;max-width: 350px"></el-image>
                  </el-popover>
                </template>
              </el-table-column>
              <el-table-column label="商品名称" prop="productName"></el-table-column>
              <el-table-column label="可用库存" width="140" prop="stock"></el-table-column>
              <el-table-column label="商品售价（元）" width="240">
                <template slot-scope="scope">{{'￥' +  scope.row.minPrice + ' ~ ￥' + scope.row.maxPrice }}</template>
              </el-table-column>
              <el-table-column label="操作" width="100">
                <template slot-scope="scope">
                  <el-button type="text" size="small" :disabled="form.couponStatus != 0 " @click="deletePro(1,scope.$index)">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-form-item>
        <el-form-item v-if="form.productType == 2" label="选择商品：" required>
          <div><el-button :disabled="form.couponStatus != 0 " @click="addCom(2)">添加商品</el-button></div>
          <div v-if="unUserProList.length > 0" class="mt10">
            <el-table :data="unUserProList" style="width: 100%">
              <el-table-column label="商品图片" width="140">
                <template slot-scope="scope">
                  <el-popover placement="right" title trigger="hover">
                    <el-image
                      slot="reference"
                      :src="scope.row.imageUrl"
                      :alt="scope.row.imageUrl"
                      style="max-height: 40px;max-width: 40px"
                    ></el-image>
                    <el-image :src="scope.row.imageUrl" style="max-height: 350px;max-width: 350px"></el-image>
                  </el-popover>
                </template>
              </el-table-column>
              <el-table-column label="商品名称" prop="productName"></el-table-column>
              <el-table-column label="可用库存" width="140" prop="stock"></el-table-column>
              <el-table-column label="商品售价（元）" width="240">
                <template slot-scope="scope">{{'￥' +  scope.row.minPrice + ' ~ ￥' + scope.row.maxPrice }}</template>
              </el-table-column>
              <el-table-column label="操作" width="100">
                <template slot-scope="scope">
                  <el-button type="text" size="small" :disabled="form.couponStatus != 0 " @click="deletePro(2,scope.$index)">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-form-item>
        <el-form-item label="使用门槛：" required>
          <el-radio-group v-model="form.fullAmountType">
            <el-radio :disabled="form.couponStatus != 0 " :label="0">无门槛使用</el-radio>
            <el-radio :disabled="form.couponStatus != 0 " :label="1">订单满
              <el-input-number 
              :disabled="form.couponStatus != 0 "
              v-model="form.fullAmountNum" 
              :precision="2" 
              :max="10000"
              :controls="false"
            ></el-input-number>
            元</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="优惠内容：" required>
          减免
          <el-input-number 
            :disabled="form.couponStatus != 0 "
            v-model="form.discountAmount" 
            :precision="2" 
            :max="10000"
            :controls="false"
          ></el-input-number>
          元
          <div class="infoFont">优惠券面值必须大于等于 0元的正整数, 且最大不能超过10000元</div>
        </el-form-item>
        <el-form-item label="用券时间：" required>
          <el-radio-group v-model="form.timeType">
            <el-radio :disabled="form.couponStatus != 0 " :label="0">指定日期</el-radio>
            <el-radio :disabled="form.couponStatus != 0 " :label="1">
              领券当日起
              <el-input-number 
                v-model="form.limitTime1" 
                :max="30"
                :min="1"
                :controls="false"
              ></el-input-number>
              天内可用
              <el-tooltip class="item" effect="dark" content="有效期按自然天计算。例如：如设置领券当日起3天内可用，用户在1月1日08:00时领取优惠券，则该优惠券可用时间为1月1日08:00至1月3日的23:59:59。" placement="top">
                <i class="el-icon-question curP"></i>
              </el-tooltip>
            </el-radio>
            <el-radio :disabled="form.couponStatus != 0 " :label="2">
              领券次日起
              <el-input-number 
                v-model="form.limitTime2" 
                :max="30"
                :min="1"
                :controls="false"
              ></el-input-number>
              天内可用
              <el-tooltip class="item" effect="dark" content="有效期按自然天计算。例如：如设置领券次日起3天内可用，用户在1月1日14:00时领取优惠券，则该优惠券可用时间为1月2日00:00至1月4日的23:59:59。" placement="top">
                <i class="el-icon-question curP"></i>
              </el-tooltip>
            </el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item v-if="form.timeType == 0" class="time" label="开始日期：" required>
          <el-date-picker
            :disabled="form.couponStatus != 0 "
            v-model="form.beginTime"
            type="datetime"
            placeholder="开始日期"
            align="right"
            value-format="yyyy-MM-dd HH:mm:ss"
            :picker-options="pickerOptions">
          </el-date-picker>
        </el-form-item>
        <el-form-item v-if="form.timeType == 0" class="time" label="结束日期：" required>
          <el-date-picker
            v-model="form.endTime"
            type="datetime"
            placeholder="结束日期"
            align="right"
            value-format="yyyy-MM-dd HH:mm:ss"
            :picker-options="pickerOptions">
          </el-date-picker>
        </el-form-item>
      </el-card>
      <el-card class="filter-container mt10" shadow="never">
        <div class="pageHead">领取及使用规则</div>
        <el-form-item label="领取人限制：" required>
          <el-radio-group v-model="form.userType">
            <el-radio :disabled="form.couponStatus != 0 " :label="0">不限制，全部人可领取</el-radio>
            <el-radio :disabled="form.couponStatus != 0 " :label="1">指定用户身份领取</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item v-if="form.userType == 1" label="选择客户：" required>
          <div><el-button :disabled="form.couponStatus != 0 " @click="customerShow = true">添加客户</el-button></div>
          <div class="mt10" v-if="customerList.length > 0">
            <el-table
              ref="multipleTable"
              :data="customerList"
              tooltip-effect="dark"
              style="width: 100%">
              <el-table-column label="用户昵称" prop="nikeName"></el-table-column>
              <el-table-column label="联系方式" width="240">
                <template slot-scope="scope">{{scope.row.cellPhone ? scope.row.cellPhone : '-'}}</template>
              </el-table-column>
              <el-table-column label="来源" prop="sourType"></el-table-column>
              <el-table-column label="客户身份" prop="vipTypeName"></el-table-column>
              <el-table-column label="注册时间" prop="createTime"></el-table-column>
              <el-table-column label="操作" width="100">
                <template slot-scope="scope">
                  <el-button :disabled="form.couponStatus != 0 " type="text" size="small" @click="deleteCus(scope.$index)">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-form-item>
        <el-form-item label="每人限领次数：" required>
          <el-radio-group v-model="form.limitType">
            <el-radio :disabled="form.couponStatus != 0 " :label="'0'">不限领次数</el-radio>
            <el-radio :disabled="form.couponStatus != 0 " :label="'1'">每人限领
              <el-input-number 
                v-model="form.limitNumber" 
                step-strictly
                :max="10"
                placeholder="限领次数上限为10"
                :controls="false"
              ></el-input-number>
              次
            </el-radio>
          </el-radio-group>
        </el-form-item>
      </el-card>
      <el-form-item class="mt20 flex-row-center">
        <el-button type="primary" :loading="loading" @click="onSubmit">保 存</el-button>
      </el-form-item>
    </el-form>
    <!-- 商品列表弹出框 -->
    <commodity :commodityShow="commodityShow" :commodityIds="commodityIds" @commodityClose="commodityClose" @commodityFun="commodityFun"></commodity>
    <!-- 客户列表弹出框 -->
    <customer :customerShow="customerShow" :customerIds="customerIds" @customerClose="customerClose" @customerFun="customerFun"></customer>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import commodity from './components/commodity';
import customer from './components/customer';
import { 
  getCouponInfoPage,
  editCouponInfo,
  findCouponInfo
} from '@/api/marketManage/coupon'
export default {
  data(){
    return{
      form:{
        productType:0,      // 适用商品
        fullAmountType:0,       // 使用门槛
        timeType:0,         // 用券时间
        time:[],
        userType:0,         // 领取人
        limitType:'0',        // 限领次数
      },
      useProList:[],       // 可用商品列表
      useIds:[],
      unUserProList:[],     // 不可用商品列表
      unUseIds:[],      
      commodityIds:[],
      commodityShow:false,
      loading:false,

      customerShow:false,
      customerIds:[],
      customerList:[],

      oldCouponCount:'',        //
      oldEndTime:'',
      oldLimitTime1:'',
      oldLimitTime2:'',
      pickerOptions: {
        shortcuts: [{
          text: '今天',
          onClick(picker) {
            picker.$emit('pick', new Date());
          }
        }, {
          text: '昨天',
          onClick(picker) {
            const date = new Date();
            date.setTime(date.getTime() - 3600 * 1000 * 24);
            picker.$emit('pick', date);
          }
        }, {
          text: '一周前',
          onClick(picker) {
            const date = new Date();
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', date);
          }
        }]
      },

    }
  },
  created(){
    this.form.couponType = 0
    this.form.shopId = this.shop.shopId
    if(this.$route.query.couponId) this.getData()
  },
  methods:{
    getData(){
      findCouponInfo(this.$route.query.couponId).then(res=>{
        console.log('优惠券详情',res)
        if(res.code == 200){
          let form = res.data
          this.oldCouponCount = res.data.couponCount
          this.oldEndTime = res.data.endTime
          // 商品
          if(res.data.productType == 1){
            this.useProList = res.data.couponProducts
            let ids = []
            this.useProList.forEach(item => ids.push(item.productId))
            form.productIds = ids;
            this.useIds = ids;
          }
          if(res.data.productType == 2){
            this.unUserProList = res.data.couponProducts
            let ids = []
            this.unUserProList.forEach(item => ids.push(item.productId))
            form.productIds = ids;
            this.unUseIds = ids;
          }
          // 使用门槛
          if(res.data.fullAmount == 0){
            form.fullAmountType = 0
          }else{
            form.fullAmountType = 1
            form.fullAmountNum = form.fullAmount
          }
          // 时间
          if(res.data.timeType == 1) {
            form.limitTime1 = res.data.limitTime
            this.oldLimitTime1 = res.data.limitTime
          }
          if(res.data.timeType == 2) {
            form.limitTime2 = res.data.limitTime
            this.oldLimitTime2 = res.data.limitTime
          }
          console.log(form.limitTime1)
          // 客户
          this.customerList = res.data.couponUsers
          this.customerList.forEach(item => this.customerIds.push(item.userId))
          // 限领次数
          if(res.data.limitNumber == 0){
            form.limitType = '0'
          }else{
            form.limitType = '1'
            form.limitNumber = form.limitNumber
          }
          console.log('type',typeof form.limitType,form.limitType)
          this.form = form
        }
      })
    },
    // 创建提交
    onSubmit(){
      console.log('form',this.form)
      // 优惠券名称
      if(!this.form.couponName){
        this.$message.error('请输入优惠券名称');
        return;
      }
      // 发放总量
      if(!this.form.couponCount){
        this.$message.error('请输入优惠券发放总量，1 < 总量 < 100000000');
        return;
      }
      // 总量判断是否是进行中
      if(this.form.couponStatus != 0){
        if(this.form.couponCount < this.oldCouponCount){
          this.$message.error('进行中的优惠券总量只能比之前的发放总量多，不可减少');
          return;
        }
      }
      // 适用商品
      if(this.form.productType == 1 ){
        if(this.useIds.length == 0){
          this.$message.error('请添加指定可用的商品');
          return;
        }
        this.form.productIds = this.useIds
      }
      if(this.form.productType == 2){
        if(this.unUseIds.length == 0){
          this.$message.error('请添加指定不可用的商品');
          return;
        }
        this.form.productIds = this.unUseIds
      }
      // 使用门槛
      if(this.form.fullAmountType == 0) this.form.fullAmount = 0
      if(this.form.fullAmountType == 1){
        if(!this.form.fullAmountNum){
          this.$message.error('请输入使用门槛金额');
          return;
        }
        this.form.fullAmount = this.form.fullAmountNum
      }
      // 优惠金额
      if(!this.form.discountAmount){
        this.$message.error('请输入优惠金额，优惠券面值必须大于等于 0元的正整数, 且最大不能超过10000元');
        return;
      }
      // 用券时间
      // 指定日期
      if(this.form.timeType == 0){
        if(!this.form.beginTime){
          this.$message.error('请选择开始日期');
          return;
        }
        if(!this.form.endTime){
          this.$message.error('请选择结束日期');
          return;
        }
        let oDate1 = new Date(this.oldEndTime);
        let oDate2 = new Date(this.form.endTime);
        if(oDate1.getTime() > oDate2.getTime()){
          this.$message.error('进行中的优惠券只能延长不可比当前时间小');
          return;
        }
      }
      // 当日
      if(this.form.timeType == 1){
        if(!this.form.limitTime1){
          this.$message.error('请输入当日天数, 1 < 天数 < 30');
          return;
        }
        if(this.form.limitTime1 < this.oldLimitTime1){
          this.$message.error('进行中的优惠券时间只能延后');
          return;
        }
        this.form.limitTime = this.form.limitTime1
      }
      // 次日
      if(this.form.timeType == 2){
        if(!this.form.limitTime2){
          this.$message.error('请输入次日天数, 1 < 天数 < 30');
          return;
        }
        if(this.form.limitTime2 < this.oldLimitTime2){
          this.$message.error('进行中的优惠券时间只能延后');
          return;
        }
        this.form.limitTime = this.form.limitTime2
      }
      // 领取人限制
      if(this.form.userType == 1){
        if(this.customerIds.length == 0){
          this.$message.error('请添加指定用户');
          return;
        }
        this.form.userIds = this.customerIds
      }
      // 领取次数
      if(this.form.limitType == 0){
        this.form.limitNumber = 0
      }
      if(this.form.limitType == 1){
        if(!this.form.limitNumber){
          this.$message.error('请请输入限领次数, 1 < 次数 < 10');
          return;
        }
      }
      this.loading = true;
      console.log('验证成功',this.form)
      editCouponInfo(this.form).then(res=>{
        console.log('编辑优惠券',res)
        if(res.code == 200){
          this.$message({
            message: '保存成功',
            type: 'success'
          });
          this.$router.replace({
            path:'/marketManage/coupon'
          })
          setTimeout(()=>{
            this.loading = false;
          },1000)
        }else{
          this.$message.error(res.message);
          this.loading = false;
        }
      })
    },
    // 客户弹出框关闭
    customerClose(){
      this.customerShow = false
    },
    customerFun(e){
      console.log('父接受kehu ',e)
      e.forEach(item =>{
        this.customerIds.push(item.userId)
        this.customerList.push(item)
      })
      this.customerShow = false
    },
    // 商品列表删除
    deleteCus(i){
      this.customerList.splice(i, 1)
      this.customerIds.splice(i, 1)
    },
    // 商品弹出框展示
    addCom(i){
      if(i == 1) this.commodityIds = this.useIds
      if(i == 2) this.commodityIds = this.unUseIds
      this.commodityShow = true;
    },
    // 关闭商品组件
    commodityClose(){
      this.commodityShow = false
    },
    // 商品组件确定回调
    commodityFun(e){
      console.log('父级接受商品组件回调',e)
      if(this.form.productType == 1){
        console.log('可用商品')
        e.forEach(item =>{
          this.useIds.push(item.productId)
          this.useProList.push(item)
        })
      }
      if(this.form.productType == 2){
        console.log('不可用商品')
        e.forEach(item => {
          this.unUseIds.push(item.productId)
          this.unUserProList.push(item)
        })
      }
      this.commodityShow = false
    },
    // 商品列表删除
    deletePro(i1,i2){
      console.log('index',i2)
      if(i1 == 1){
        this.useProList.splice(i2, 1)
        this.useIds.splice(i2, 1)
      }
      if(i1 == 2){
        this.unUserProList.splice(i2, 1)
        this.unUseIds.splice(i2, 1)
      }
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
  components:{
    commodity,
    customer
  },
}
</script>
<style lang="less" scoped>
.inner /deep/ .el-input__inner{
  text-align: left;
}
.time /deep/ .el-range-separator{
  line-height: 22px;
}
</style>